/*
0 - 600px         mobile
600px - 900px     tablet portrait
900px - 1200px    tablet ladscape
1200px - 1800px   is where my normal styles apply
1800px +          large desctop
*/
/*
  Breackpoints argument choices
- phone
- tab-port
- tab-land
- lg-desktop

ORDER: base > typography > general > layout> page layout > components

1em = 16px
*/
:root {
  --color-primary: #eb2f64;
  --color-primary-light: #ff3366;
  --color-primary-dark: #ba265d;
  --color-grey-light-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #cccccc;
  --color-grey-dark-1: #333333;
  --color-grey-dark-2: #777777;
  --color-grey-dark-3: #999999;
  --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, .3);
  --shadow-light: 0 2rem 5rem rgba(0, 0, 0, .25);
  --line: 1px solid var(--color-grey-light-2);
}

* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark));
  background-size: cover;
  background-repeat: no-repeat;
}

a {
  display: inline-block;
  position: relative;
  color: inherit;
  text-decoration: none;
}

.container {
  max-width: 120rem;
  margin: 8rem auto;
  background-color: var(--color-grey-light-2);
  min-height: 30rem;
  box-shadow: var(--shadow-dark);
  border-radius: .2rem;
}

@media only screen and (max-width: 1200px) {
  .container {
    margin: 0;
    max-width: 100%;
  }
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/Open_Sans/");
  src: local("../fonts/Open_Sans/Open_Sans-SemiBold"), url("../fonts/Open_Sans/OpenSans-SemiBold.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/Open_Sans/");
  src: local("../fonts/Open_Sans/Open_Sans-Regular"), url("../fonts/Open_Sans/OpenSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/Open_Sans/");
  src: local("../fonts/Open_Sans/Open_Sans-Light"), url("../fonts/Open_Sans/OpenSans-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 1100px) {
  html {
    font-size: 50%;
  }
}

body {
  font-family: "Open Sans", "Arial", sans-serif, serif;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.6;
  color: var(--color-grey-dark-2);
}

@keyframes pulsate {
  0% {
    transform: scale(1);
    box-shadow: none;
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
  }
  100% {
    transform: scale(1);
    box-shadow: none;
  }
}

.modal {
  width: 29rem;
  border-radius: .5rem;
  background-color: red;
  overflow: hidden;
}

.header {
  height: 7rem;
  background-color: #fff;
  border-bottom: var(--line);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 500px) {
  .header {
    flex-wrap: wrap;
    height: 12rem;
    align-content: space-around;
  }
}

.logo {
  height: 3.2rem;
  margin-left: 2rem;
}

.search {
  flex: 0 0 40%;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 500px) {
  .search {
    order: 1;
    flex: 0 0 100%;
    background-color: var(--color-grey-light-2);
  }
}

.search__input {
  font-family: inherit;
  font-size: inherit;
  background-color: var(--color-grey-light-2);
  border: none;
  padding: .7rem 2rem;
  border-radius: 100px;
  width: 90%;
  margin-right: -3rem;
  transition: width .4s;
}

.search__input:focus {
  outline: none;
  width: 100%;
  background-color: var(--color-grey-light-3);
}

.search__input::-webkit-input-placeholder {
  color: var(--color-grey-light-4);
  font-weight: 100;
}

.search__input:focus + .search__btn {
  background-color: var(--color-grey-light-3);
}

.search__btn {
  border: none;
  background-color: var(--color-grey-light-2);
}

.search__btn:focus {
  outline: none;
}

.search__btn:active {
  transform: translateY(2px);
}

.search__icon {
  width: 2rem;
  height: 2rem;
  fill: var(--color-grey-dark-3);
}

.user-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}

.user-nav > * {
  padding: 0 2rem;
  cursor: pointer;
  height: 100%;
  display: flex;
  align-items: center;
}

.user-nav > *:hover {
  background-color: var(--color-grey-light-2);
}

.user-nav__icon-box {
  position: relative;
}

.user-nav__icon {
  width: 2.25rem;
  height: 2.25rem;
  fill: var(--color-grey-dark-2);
}

.user-nav__notification {
  font-size: 0.8rem;
  height: 1.75rem;
  width: 1.75rem;
  border-radius: 50%;
  background-color: var(--color-primary);
  color: #fff;
  position: absolute;
  top: 1.5rem;
  right: 1.1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.user-nav__user-photo {
  height: 3.75rem;
  border-radius: 50%;
  margin-right: 1rem;
}

.sidebar {
  background-color: var(--color-grey-dark-1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 18%;
}

.side-nav {
  font-size: 1.4rem;
  list-style: none;
  padding-top: 3.5rem;
}

@media only screen and (max-width: 900px) {
  .side-nav {
    display: flex;
    padding: 0;
  }
}

.side-nav__item {
  position: relative;
}

@media only screen and (max-width: 900px) {
  .side-nav__item {
    flex: 1;
  }
}

.side-nav__item:not(:last-child) {
  margin-bottom: .5rem;
}

@media only screen and (max-width: 900px) {
  .side-nav__item:not(:last-child) {
    margin-bottom: 0;
  }
}

.side-nav__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0.3rem;
  background-color: var(--color-primary);
  transform: scaleY(0);
  transition: transform 0.4s, width 0.4s cubic-bezier(1, 0, 0, 1) 0.2s, background-color 0.1s;
}

.side-nav__item:hover::before, .side-nav__item--active::before {
  transform: scaleY(1);
  width: 100%;
}

.side-nav__item:active::before {
  background-color: var(--color-primary-light);
}

.side-nav__link:link, .side-nav__link:visited {
  text-decoration: none;
  text-transform: uppercase;
  padding: 1.5rem 2rem;
  color: var(--color-grey-light-1);
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 900px) {
  .side-nav__link:link, .side-nav__link:visited {
    justify-content: center;
    padding: 2rem;
  }
}

@media only screen and (max-width: 575px) {
  .side-nav__link:link, .side-nav__link:visited {
    flex-direction: column;
    padding: 1rem;
    text-align: center;
  }
}

.side-nav__icon {
  width: 2.5rem;
  height: 2.5rem;
  fill: var(--color-grey-light-1);
  margin-right: 2rem;
}

@media only screen and (max-width: 575px) {
  .side-nav__icon {
    margin-right: 0;
    margin-bottom: .7rem;
    width: 1.5rem;
    height: 1.5rem;
  }
}

.legal {
  font-size: 1.2rem;
  text-align: center;
  padding: 2.5rem;
  color: currentColor;
}

@media only screen and (max-width: 900px) {
  .legal {
    display: none;
  }
}

.content {
  display: flex;
}

@media only screen and (max-width: 900px) {
  .content {
    flex-direction: column;
  }
}

.gallery {
  display: flex;
}

.gallery__photo {
  width: 100%;
  display: block;
}

.overview {
  display: flex;
  border-bottom: 1px solid var(--color-grey-light-2);
  align-items: center;
}

.overview__heading {
  font-size: 2.25rem;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 1.5rem 3rem;
}

@media only screen and (max-width: 900px) {
  .overview__heading {
    font-size: 1.8rem;
    padding: 1.25rem 2rem;
  }
}

.overview__heading__stars {
  margin-right: auto;
  display: flex;
  align-items: center;
}

.overview__icon-star, .overview__icon-location {
  width: 1.75rem;
  height: 1.75rem;
  fill: var(--color-primary);
}

.overview__location {
  font-size: 1.2rem;
  display: flex;
  align-items: center;
}

.overview__icon-location {
  margin-right: .5rem;
}

.overview__rating {
  background-color: var(--color-primary);
  margin-left: 3rem;
  color: #fff;
  align-self: stretch;
  padding: 0 2.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@media only screen and (max-width: 900px) {
  .overview__rating {
    padding: 0 1.5rem;
  }
}

.overview__rating-average {
  font-size: 2.5rem;
  font-weight: 300;
  margin-bottom: -3px;
}

@media only screen and (max-width: 900px) {
  .overview__rating-average {
    font-size: 1.5rem;
  }
}

.overview__rating-count {
  font-size: 0.8rem;
  text-transform: uppercase;
}

@media only screen and (max-width: 900px) {
  .overview__rating-count {
    font-size: .5rem;
  }
}

.btn-inline {
  border: none;
  background-color: transparent;
  color: var(--color-primary);
  font-size: inherit;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  display: inline-block;
  cursor: pointer;
  transition: all .2s;
}

.btn-inline:hover {
  color: var(--color-grey-dark-1);
}

.btn-inline:hover span {
  margin-left: .8rem;
  transition: margin-left .2s;
}

.btn-inline:focus {
  outline: none;
  animation: pulsate 1s infinite;
}

.btn-inline span {
  margin-left: 0.3rem;
}

.detail {
  display: flex;
  padding: 4.5rem;
  background-color: var(--color-grey-light-1);
  border-bottom: var(--line);
}

@media only screen and (max-width: 900px) {
  .detail {
    padding: 3rem;
  }
}

@media only screen and (max-width: 600px) {
  .detail {
    flex-direction: column;
  }
}

.description {
  font-size: 1.4rem;
  background-color: #fff;
  box-shadow: var(--shadow-light);
  flex: 0 0 60%;
  margin-right: 4.5rem;
  padding: 3rem;
}

@media only screen and (max-width: 900px) {
  .description {
    padding: 2rem;
    margin-right: 3rem;
  }
}

@media only screen and (max-width: 600px) {
  .description {
    margin-right: 0;
    margin-bottom: 3rem;
  }
}

.user-reviews {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.paragraph:not(:last-of-type) {
  margin-bottom: 2rem;
}

.list {
  list-style: none;
  margin: 3rem 0;
  padding: 3rem 0;
  border-top: var(--line);
  border-bottom: var(--line);
  display: flex;
  flex-wrap: wrap;
}

.list__item {
  flex: 0 0 50%;
  margin-bottom: 0.7rem;
}

.list__item:before {
  content: "";
  display: inline-block;
  height: 1.5rem;
  width: 1.5rem;
  margin-right: .7rem;
  background-color: var(--color-primary);
  mask-image: url(../img/svg/chevron-thin-right.svg);
  mask-size: cover;
}

.recommend {
  font-size: 1.3rem;
  color: var(--color-grey-dark-3);
  display: flex;
  align-items: center;
}

.recommend__count {
  margin-right: auto;
}

.recommend__friends {
  display: flex;
}

.recommend__photo {
  box-sizing: content-box;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  border: .3rem solid #fff;
}

.recommend__photo:not(:last-child) {
  margin-right: -2rem;
}

.review {
  background-color: #fff;
  box-shadow: var(--shadow-light);
  padding: 3rem;
  margin-bottom: 3.5rem;
  position: relative;
  overflow: hidden;
}

@media only screen and (max-width: 900px) {
  .review {
    padding: 2rem;
    margin-bottom: 3rem;
  }
}

.review::before {
  content: "\201c";
  position: absolute;
  top: -2.75rem;
  left: -1rem;
  font-size: 20rem;
  color: var(--color-grey-light-2);
  line-height: 1;
  font-family: sans-serif;
  z-index: 1;
}

.review__text {
  font-size: 1.4rem;
  margin-bottom: 2rem;
  z-index: 2;
  position: relative;
}

.review__user {
  display: flex;
  align-items: center;
}

.review__user-box {
  margin-right: auto;
}

.review__user-name {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 500;
  margin-bottom: 0.4rem;
}

.review__user-date {
  font-size: 1rem;
  color: var(--color-grey-dark-3);
}

.review__rating {
  font-size: 2.2rem;
  color: var(--color-primary);
  font-weight: 300;
}

.review__photo {
  width: 4.5rem;
  height: 4.5rem;
  border-radius: 50%;
  margin-right: 1rem;
}

.cta {
  padding: 3.5rem 0;
  text-align: center;
}

@media only screen and (max-width: 900px) {
  .cta {
    padding: 2.5rem 0;
  }
}

.cta__book-now {
  font-size: 1.5rem;
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 2.5rem;
}

.btn {
  cursor: pointer;
  font-size: 1.5rem;
  border: none;
  text-transform: uppercase;
  font-weight: 300;
  border-radius: 100px;
  color: #fff;
  background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
  position: relative;
  overflow: hidden;
}

.btn__visible {
  display: inline-block;
  padding: 2rem 7.5rem;
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform .4s;
}

.btn__invisible {
  display: inline-block;
  position: absolute;
  left: 0;
  top: -100%;
  padding: 2rem 0;
  width: 100%;
  height: 100%;
  transition: top .4s;
}

.btn:hover {
  background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark));
}

.btn:focus {
  outline: none;
  animation: pulsate 1s infinite;
}

.btn:hover .btn__visible {
  transform: translateY(100%);
}

.btn:hover .btn__invisible {
  top: 0;
}

.hotel-view {
  background-color: #fff;
  flex-grow: 1;
}
